.todoItem {
    background-color: #e6e6e6;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 32px rgba(32, 35, 41, 0.15);
  }

  .todoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .todoItem-p--complete {
    text-decoration: line-through;
  }

  .icon {
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20px;
    width: 20px;
    font-size: 24px;
  }

  .icon-check--active {
    color: #4caf50;
  }

  .icon-delete:hover {
    color: #d61313;
  }