body {
  margin: 0;
  padding: 0;
  background: #F2F4F5;
  color: #1E1E1F;
  font-family: 'Lato', sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}

.createTodoButton {
  background-color: #4caf50;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 36px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 52px;
  width: 52px;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: 0.3s ease;
  z-index: 1;
}

.createTodoButton:hover {
  background-color: #50ba54;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.todoCounter {
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
}
.todoItem {
    background-color: #e6e6e6;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 32px rgba(32, 35, 41, 0.15);
  }

  .todoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .todoItem-p--complete {
    text-decoration: line-through;
  }

  .icon {
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20px;
    width: 20px;
    font-size: 24px;
  }

  .icon-check--active {
    color: #4caf50;
  }

  .icon-delete:hover {
    color: #d61313;
  }
ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
}
.todoSearch {
    background: #F9FBFC;
    border-radius: 4px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 2px 12px rgba(32, 35, 41, 0.25);
    outline: none;
  }

  .todoSearch::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
  }

  .todoSearch:-ms-input-placeholder {
    color: #A5A5A5;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
  }

  .todoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
  }
.modalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffffff;
  }
form {
    width: 900%;
    max-width: 500px;
    background-color: #F9FBFC;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
}

label {
    text-align: center;
    font-weight: bold;
    color: #1E1E1F;
    margin-bottom: 24px;
}

textarea {
    background-color: #F9FBFC;
    border: 2px, solid #202329;
    border-radius: 2px;
    box-shadow: 0px 4px 16px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 16px;
    text-align: center;
    padding: 12px;
    outline: none;
    height: 124px;
    width: calc(100%-25px);
}

textarea::-webkit-input-placeholder {
    color: #a5a5a5;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
}

textarea:-ms-input-placeholder {
    color: #a5a5a5;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
}

textarea::placeholder {
    color: #a5a5a5;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
}

.todoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.todoForm-button {
    border-radius: 12px;
    border: none;
    color: #1E1E1F;
    cursor: pointer;
    display: inline-block;
    font-family: 'Lato', sans-serif;
    font-size: 24px;
    margin: 0 4px 0 4px;
    height: 48px;
    width: 120px;
}

.todoForm-button-add {
    background-color: #4caf50;
}

.todoForm-button-cancel {
    background-color: #d61313;
}

footer {
    position: fixed;
    left: 0;
    bottom: 0;
    height: 24px;
    width: 100%;
    background-color: #e6e6e6;
    color: #1E1E1F;
    text-align: center;
}

a {
    color: #1E1E1F;
    font-weight: bold;
    text-decoration: none;
}

a:hover {
    color: #4caf50;
}

